// 过滤器function
// 普通用户来源（）
export const userForm = type => {
    type=type*1
    let str = "";
    switch (type) {
        case 1:
            str = "APP用户";
            break;
        case 2:
            str = "微信用户";
            break;
        case 3:
            str = "卡片用户";
            break;
        case 4:
            break;
        case 5:
            break;
        default:
            str = type;
            break;
    }
    return str;
};
// 普通用户来源（app）
export const userFormApp = type => {
    type=type*1
    switch (type) {
        case 0:
            return "回收端";
            break;
        case 1:
            return "用户端";
            break;
        case 2:
            return "分拣-管理";
            break;
        case 3:
            return "兑换端";
            break;
        case 4:
            return "分拣-司机";
            break;
        default:
            return type;
            break;
    }
};

// 订单状态（app）
export const orderType = type => {
    type=type*1;
    switch (type) {
        case 1:
            return "待接单";
            break;
        case 2:
            return "待上门";
            break;
        case 3:
            return "待称重";
            break;
        case 4:
            return "待付款";
            break;
        case 5:
            return "已取消";
            break;
        case 6:
            return "已完成";
            break;
        case 7:
            return "待上门核重";
        case 8:
            return "待核重";
        case 9:
            return "已派单，待接单";
        case 10:
            return "司机已拒单";
        default:
            return type;
            break;
    }
};

