import Vue from "vue";
import Router from "vue-router";

// const Login = () => import("./views/login/login.vue");
const Example = () => import("@/views/example.vue");
const Main = () => import("@/views/main");
import LoginRouter from "./modules/login.router";
import UserRouter from "./modules/user.router";

Vue.use(Router);

/**
 * 路由字段配置说明
 *
 * @param {Object} meta 路由元数据
 *
 * @param {Boolean} meta.unfold 是否可以在菜单栏展开
 * @param {Boolean} meta.hide 是否可以在菜单栏显示
 * @param {Boolean} meta.icon 菜单栏图标
 *
 * @param {Boolean} children.meta.hide 是否可以在菜单栏显示
 *
 */

const router = new Router({
    mode: "hash",
    routes: [
        {
            path: "/",
            redirect: "/login",
            name: "main",
            component: Main,
            meta: {}
        },
        {
            path: "/example",
            component: Main,
            name: "example",
            meta: {
                name: "example",
                hide: false,
                iconType: "warning"
            },
            redirect: {
                name: "test"
            },
            children: [
                {
                    path: "",
                    name: "test",
                    meta: {
                        name: "example"
                    },
                    component: Example
                }
            ]
        }
    ].concat(
        LoginRouter,
        UserRouter
    )
});
export default router;
