/****
 * 工具类
 */
import api from "@/api";
const Utils = {
    /**
     * 对象的深度拷贝
     * @param data 需要拷贝的元数据
     * @return {any} 返回拷贝后的新数据
     */
    deepClone(data) {
        const type = this.getType(data);
        let obj;
        if (type === "array") {
            obj = [];
        } else if (type === "object") {
            obj = {};
        } else {
            //不再具有下一层次
            return data;
        }
        if (type === "array") {
            for (let i = 0, len = data.length; i < len; i++) {
                obj.push(this.deepClone(data[i]));
            }
        } else if (type === "object") {
            for (let key in data) {
                obj[key] = this.deepClone(data[key]);
            }
        }
        return obj;
    },

    /**
     * 判断对象类型
     * @param {Object} object
     * @return {String} object type
     */
    getType(object) {
        var toString = Object.prototype.toString;
        var map = {
            "[object Boolean]": "boolean",
            "[object Number]": "number",
            "[object String]": "string",
            "[object Function]": "function",
            "[object Array]": "array",
            "[object Date]": "date",
            "[object RegExp]": "regExp",
            "[object Undefined]": "undefined",
            "[object Null]": "null",
            "[object Object]": "object"
        };
        if (object instanceof Element) {
            return "element";
        }
        return map[toString.call(object)];
    },
    /**
     * 接口数据返回后处理赋值
     * @param {Array} arr 页面所需字段列表
     * @param {Object} obj 接口获得的数据
     * @return {Object} 页面所需的数据
     */
    getRequestData(arr = [], obj = {}) {
        if (arr.length < 1) {
            return obj;
        } else if (JSON.stringify(obj) === "{}") {
            arr.forEach(el => {
                obj[el] = "";
            });
            return obj;
        } else {
            let data = {};
            arr.forEach(el => {
                data[el] = obj[el] + "";
            });
            return data;
        }
    },
    // 返回上一页
    /**
     *
     * @param {路由守卫next回调} next
     * @param {vue} that
     */
    showConfirm(next, that) {
        that.$confirm({
            title: "确定要离开当前页面吗？",
            content: "离开后您将取消本次编辑的全部内容，确定要离开吗？",
            cancelText: "取消",
            okText: "确定",
            centered: true,
            onOk() {
                next();
            },
            onCancel() {
                that.$router.go(1);
            },
            class: "test"
        });
    },
    /**
     *
     * @param 根据数组获得省市区
     * @param {vue} that
     */
    getAddress(arr, level, options, cb) {
        let community = this.deepClone(arr);
        if (arr.length > 0 && community[0]) {
            let promiseList = [];
            let resultArr = [];
            for (let i = 0; i < arr.length - 1; i++) {
                const el = arr[i]*1;
                if (!el)break;
                if (i < 2) {
                    promiseList.push(api.common.cityList({ parent_id: el }));
                } else {
                    promiseList.push(
                        api.common.streetList({
                            parent_id: i === 2 ? el * 1000000 : el
                        })
                    );
                }
            }
            Promise.all(promiseList).then(data => {
                if (data.length) {
                    data.forEach(item => {
                        item.data.forEach(it => {
                            it.id += "";
                        });
                        resultArr.push(item.data);
                    });
                    getAdd();
                }else{
                    cb && cb(options);
                }
            });
            function getAdd() {
                for (let i = resultArr.length - 2; i > -1; i--) {
                    const el = resultArr[i];
                    for (let z = 0; z < el.length; z++) {
                        el[z].isLeaf = false;
                        if (
                            el[z].id * 1 ===
                            (resultArr[i + 1][0].parent_id + "").substr(0, 6) *
                                1
                        ) {
                            el[z].children = resultArr[i + 1];
                        }
                    }
                };
                options.forEach((item, index) => {
                    if (item.id * 1 === community[0] * 1) {
                        item.children = resultArr[0];
                    }
                });

                cb && cb(options);
            }
        }
    }
};

export default Utils;
