<template>
    <div class="wrap">
        <!-- 面包屑下标题 -->
        <div class="wrap-title">
            <span class="wrap-title-text">
                {{ title }}
            </span>
        </div>

        <!-- header -->
        <div class="wrap-header">
            <slot name="header"></slot>
        </div>

        <!-- container-->
        <div class="wrap-cont">
            <div class="wrap-cont-cover">
                <div class="wrap-container">
                    <!-- 默认插槽 -->
                    <slot></slot>
                </div>
            </div>
        </div>
    </div>
</template>

<script type="text/javascript">
    export default {
        name: "contentComponent",
        data() {
            return {
                title: ""
            };
        },
        watch: {
            $route(route) {
                this.title = route.meta.name || "";
            }
        },
        mounted() {
            this.title = this.$route.meta.name || "";
            // 更新 title
            this.$eventBus.on("updateTitle", title => {
                this.title = title;
            });
        }
    };
</script>

